  font-family: 'Roboto', sans-serif;
  font-weight: 600;
}
#app{
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.timer .box {
  display: inline-block;
  margin: 1rem;
  width: 10rem;
  text-align: center;
  background: #E5989B;
  -moz-transform-style: preserve-3d;
}
.timer .box .spacer {
  position: relative;
  content: '';
  display: block;
  height: 20px;
  background: #FFB4A2;
}
.timer .box .spacer::before, .timer .box .spacer::after {
  content: '';
  position: absolute;
  top: 5px;
  width: 10px;
  height: 10px;
  background: #FFCDB2;
  border-radius: 50%;
}
.timer .box .value {
  position: relative;
  margin: 0;
  padding: 1.2rem 0 2rem;
  font-family: "Graduate", sans-serif;
  font-size: 3em;
  color: rgba(109, 104, 117, 0.6);
  background: #FFB4A2;
  -webkit-transform-origin: top center;
  transform-origin: top center;
  -webkit-animation: wind 4s ease-out alternate infinite;
  animation: wind 4s ease-out alternate infinite;
  -webkit-box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
          box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
}
.timer .box .label {
  margin: 0;
  padding: 1rem;
  color: rgba(255, 255, 255, 0.8);
  background: #E5989B;
}
@-webkit-keyframes wind {
0% {
    -webkit-transform: rotatex(0);
    transform: rotatex(0);
    -webkit-box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
            box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
    color: rgba(109, 104, 117, 0.6);
    background: #FFB4A2;
}
10% {
    -webkit-transform: rotatex(20deg);
    transform: rotatex(20deg);
    -webkit-box-shadow: 0 15px 10px -10px #ab737e;
            box-shadow: 0 15px 10px -10px #ab737e;
    color: rgba(109, 104, 117, 0.45);
    background: #ffc0b1;
}
}
@keyframes wind {
0% {
    -webkit-transform: rotatex(0);
    transform: rotatex(0);
    -webkit-box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
            box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
    color: rgba(109, 104, 117, 0.6);
    background: #FFB4A2;
}
10% {
    -webkit-transform: rotatex(20deg);
    transform: rotatex(20deg);
    -webkit-box-shadow: 0 15px 10px -10px #ab737e;
            box-shadow: 0 15px 10px -10px #ab737e;
    color: rgba(109, 104, 117, 0.45);
    background: #ffc0b1;
}
}
